@import '../ScssVariables.scss';
@import '../LanguageBar/LanguageBar.scss';

.Hcontainer {
   display: flex;
   justify-content: center;
   overflow-x: hidden;

   a, p, button {
      font-family: $textFontFamily;
   }

   .backgroundImage {
      img {
         width: 100%;
         max-width: 1440px;
      }
   }

   .backgroundImage2 {
      display: none;
      img {
         width: 100%;
         max-width: 1440px;
      }
   }
   .rightPart {
      display: flex;
      align-items: center;
   }

   @media (max-width: 1280px) {
      .backgroundImage {
         display: none;
      }
      .backgroundImage2 {
         display: block;
      }

   }

   .navigationBar {
      position: absolute;
      top: 0;
      display: flex;
      align-items: center;
      padding: 40px 60px;

      .logo {
         margin-right: 20px;
         img{
            cursor: pointer;
            width: 50px;
         }
      }

      .closeBtn {
         display: none;
         color: white;
         font-size: 30px;
         margin: 10px 300px 0 5px;
         width: 45px;
         height: 45px;
         cursor: pointer;

         &:hover {
            color: $orangeColor;
            background-color: white;
            border-radius: 100px;
         }

         @media (max-width: 1280px) {
            text-decoration: none;
            width: 45px;
            height: 45px;
            margin-left: 7px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            border-radius: 50%;
            transition: 0.3s;
         }
      }



      .nav_links {
         list-style: none;

         .navBtn {
            margin: 10px;
            transition: 0.3s;
            cursor: pointer;

            &:not(:last-child) {
               margin-right: 13px;
            }

            a {
               text-decoration: none;
               font-family: $NavBarFontFamily;
               font-size: 20px;
               font-weight: 600;
               color: $orangeColor;
            }

            @media (max-width: 1280px) {
               background-color: #ff976d;
               padding: 10px 0;
               border-radius: 5px;
               a {
                  color: white;
               }
               &:hover {
                  a {
                     color: $orangeColor;
                  }
                  background-color: #ffffff;
                  transition: all 0.5s;
               }
            }

            @media (min-width: 1280px) {
               display: inline-block;

               &::after {
                  content: '';
                  width: 0;
                  height: 2px;
                  background: $orangeColor;
                  display: block;
                  margin: auto;
                  transition: 0.5s;
                  border-radius: 100px;
               }

               &:hover::after {
                  width: 100%;
                  transition: all 0.3s ease 0s;
               }


            }
         }

         .marked {
            @media (min-width: 1280px) {
               &::after {
                  width: 100%;
                  background: $greenColor;
               }
               a {
                  color: $greenColor;
               }
            }
         }
      }

      .btn{
         a {
            text-decoration: none;
            width: 45px;
            height: 45px;
            margin-left: 7px;
            color: $orangeColor;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            border-radius: 50%;
            transition: 0.3s;

            &:last-child {
               margin-right: 5px;
            }

            &:hover {
               background-color: $orangeColor;
               color: $backgroundColor;
            }

            i {
               font-size: 20px;
               cursor: pointer;
            }
         }
      }

      @media (max-width: 1280px) {
         display: flex;
         width: 100%;
         padding: 20px;
         justify-content: space-between;
         background-color: $backgroundColor;

         .rightPart {
            display: flex;
            align-items: center;
         }
      }
   }

   .text {
      z-index: 0;
      position: absolute;
      top: 0;
      transform: translate(40px, 250px);

      h1 {
         font-family: $TitleFontFamily;
         font-size: 53px;
         color: $orangeColor;
      }

      p {
         color: $greenColor;
         font-size: 25px;
         font-weight: 600;
         width: 500px;
      }

      button {
         margin-top: 50px;
         border: none;
         outline: none;
         background: $orangeColor;
         padding: 10px 40px;
         border-radius: 100px;
         font-size: 17px;
         color: white;
         cursor: pointer;

         &:hover {
            background: $hoverOrangeColor;
         }
      }
   }
}

.checkbtn {
   font-size: 30px;
   color: $orangeColor;
   float: right;
   line-height: 80px;
   margin-right: 10px;
   cursor: pointer;
   display: none;
}

#check {
   display: none;
}

@media (max-width: 1280px) {

   .checkbtn {
      display: block;
      margin-left: 20px;
   }

   .nav_links {
      position: fixed;
      z-index: 1;
      width: 350px;
      height: 100vh;
      background: #ff7a45;
      top: 0;
      left: -400px;
      transition: all 0.5s;
      text-align: center;

      .navBtn {
         display: block;
         margin: 50px 0;
         line-height: 30px;

         a {
            font-size: 20px;

            &:hover, &.active {
               background: none;
               color: #0082e6;
            }
         }
      }
   }

   #check:checked ~ ul {
      left: 0;
   }
}


