@import "../ScssVariables";

.FooterContainer {
   display: flex;
   align-items: center;
   justify-content: center;

   p, a {
      font-family: $textFontFamily;
   }

   Footer {
      width: 1520px;
      margin-top: 30px;
      background-color: $footerColor;

      .Fcontainer {
         display: flex;
         margin: auto;
         padding: 30px;

         .otherComponents {
            margin-right: 50px;

            .logoWithName {
               display: flex;
               cursor: pointer;
               width: 195px;
               margin-top: 10px;

               .image img {
                  width: 50px;
               }
   
               h1 {
                  font-family: 'Samsung Sharp Sans', sans-serif;
                  color: $orangeColor;
                  text-transform: capitalize;
                  position: relative;
                  margin: 25px 100px 0px 15px;
               }
            }
            
            .otherInfo {
               position: absolute;
               color: white;
               transform: translate(20px, 20px);

               ul {
                  width: 200px;

                  li {
                     display: flex;
                     list-style: none;
                     outline: none;

                     i {
                        padding-bottom: 20px;
                        font-size: 25px;
                     }

                     p {
                        padding-left: 15px;
                        font-weight: 600;
                        font-size: 18px;
                     }
                  }
               }
            }
         }

         .components {
            display: flex;
            justify-content: space-between;
            width: 100%;
            padding-top: 45px;

            .column {
               width: 100%;
               padding-left: 30px;

               &:not(:last-child) {
                  border-right: rgb(173, 173, 173) 1px solid;
               }

               h4 {
                  font-family: $TitleFontFamily;
                  font-size: 20px;
                  color: $backgroundColor;
                  text-transform: capitalize;
                  margin-bottom: 30px;
                  position: relative;

                  &::before {
                        content: '';
                        position: absolute;
                        left: 0;
                        bottom: -10px;
                        background-color: $orangeColor;
                        height: 2px;
                        box-sizing: border-box;
                        width: 50px;
                  }
               }

               ul {
                  list-style: none;

                  li {
                        padding: 5px;

                        &:not(:last-child) {
                           margin-bottom: 10px;
                        }

                        a {
                           font-size: 16px;
                           text-transform: capitalize;
                           text-decoration: none;
                           font-weight: 600;
                           color: $backgroundColor;
                           display: block;
                           transition: all 0.3s ease;

                           &:hover {
                              color: $hoverOrangeColor;
                              padding-left: 8px;
                           }
                        }
                  }
               }

               i {
                  font-size: 18px;
                  color: $orangeColor;
                  display: inline-block;
                  height: 40px;
                  width: 40px;
                  background-color: $footerColor;
                  margin: 0 10px 10px 0;
                  line-height: 40px;
                  text-align: center;
                  border-radius: 50%;
                  transition: all 0.4s ease;

                  &:hover {
                        color: white;
                        background-color: $orangeColor;
                  }
               }
            }
         }
      }
   }
}